.pink__btn {
  width: 320px;
  min-height: 50px;
  border-radius: 25px;
  font-size: 15px;
  font-weight: 600;
  line-height: 18px;
  border: none;
  padding: 0;
  align-self: center;
  cursor: pointer;
  outline: none;
  color: #f9f9f9;
  background-color: #e94646;
}
