    line-height: 19px;
  }
}
@media (min-width: 1360px) {
  .light__btn {
    width: 360px;
    min-height: 70px;
    border-radius: 35px;
    font-size: 18px;
    line-height: 19px;
    transition: color 0.6s 0.1s, background-color 0.5s 0.2s;
  }
  .light__btn:hover,
