.show__btn {
  position: relative;
  max-width: 300px;
  font-size: 11px;
  font-weight: 400;
  line-height: 32px;
  padding: 0;
  align-self: center;
  text-decoration: underline;
  border: none;
  cursor: pointer;
  outline: none;
  color: #e94646;
  background-color: transparent;
}
.show__btn::after {
  position: absolute;
  top: 15px;
  right: -10px;
  width: 5px;
  height: 4px;
  content: "";
