  .show__btn {
    font-size: 14px;
  }
  .show__btn:after {
    width: 8px;
    height: 5px;
    right: -12px;
    background-size: 8px 5px;
  }
}
@media (min-width: 1360px) {
  .show__btn {
    font-size: 16px;
  }
}
