[data-aos="border-pink"] {
  transition: border 1.2s 0.2s;
}
[data-aos="border-pink"].aos-animate {
  border: 8px solid #e94646;
}
[data-aos="border-orange"] {
  transition: border 1.2s 0.2s;
}
[data-aos="border-orange"].aos-animate {
  border: 8px solid #ff833d;
}
[data-aos="border-lightblue"] {
  transition: border 1.2s 0.2s;
}
[data-aos="border-lightblue"].aos-animate {
  border: 8px solid #86d3ff;
}
[data-aos="border-green"] {
  transition: border 1.2s 0.2s;
}
[data-aos="border-green"].aos-animate {
  border: 8px solid #66cf6a;
}
[data-aos="border-blue"] {
  transition: border 1.2s 0.2s;
}
[data-aos="border-blue"].aos-animate {
  border: 8px solid #6677cf;
}
[data-aos="border-violet"] {
  transition: border 1.2s 0.2s;
}
[data-aos="border-violet"].aos-animate {
  border: 8px solid #c368da;
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-header__wrapper {
  width: 360px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: transparent;
  min-height: 60px;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    width: 768px;
  }
}
@media (min-width: 1360px) {
  .main-header__wrapper {
    width: 1360px;
  }
}
.main-header__logo {
  width: 80px;
  height: 46px;
  margin-right: 75px;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    flex-direction: row-reverse;
    justify-content: space-between;
    min-height: 80px;
    padding: 0 60px;
  }
  .main-header__logo {
    width: 150px;
    height: 60px;
    margin: 0;
  }
}
@media (min-width: 1360px) {
  .main-header__wrapper {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    min-height: 175px;
    padding: 40px 70px;
  }
  .main-header__logo {
    width: 150px;
    height: 86px;
    margin-bottom: 25px;
  }
}
.main-nav {
  position: relative;
  background-color: #1d0f1b;
}
.main-nav--nojs {
  display: block;
}
.main-nav__toggle {
  position: absolute;
  top: -8px;
  right: 0px;
  border: none;
  width: 20px;
  height: 13px;
  padding: 0;
  cursor: pointer;
  background-color: transparent;
  background-image: url("../img/burger.svg");
  background-repeat: no-repeat;
  background-size: 20px 13px;
}
.main-nav__wrapper {
  width: 360px;
  margin: 0 auto;
  position: absolute;
  top: 30px;
  right: -20px;
  padding: 0;
  padding-left: 30px;
  background-color: #1d0f1b;
  z-index: 10000000;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    width: 768px;
  }
}
@media (min-width: 1360px) {
  .main-nav__wrapper {
    width: 1360px;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  border-left: 2px solid #e94646;
  list-style: none;
  padding: 30px 0;
  background-color: #1d0f1b;
  width: 100%;
  height: 100%;
}
.main-nav__item {
  margin-bottom: 20px;
}
.main-nav__item:last-child {
  margin-bottom: 0;
}
.main-nav__link {
  display: block;
  position: relative;
  text-decoration: none;
  width: 220px;
  color: #fefeff;
  font-size: 13px;
  font-weight: 600;
  line-height: 22px;
  padding-left: 20px;
  transition: color 0.5s 0.2s;
}
.main-nav__link:hover,
.main-nav__link:focus {
  color: #e94646;
}
.main-nav__link:active {
  opacity: 0.8;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    top: -13px;
    width: 28px;
    height: 18px;
    background-size: 28px 18px;
  }
  .main-nav__wrapper {
    position: absolute;
    top: 40px;
    left: -60px;
    padding: 0;
    padding-left: 30px;
    background-color: #1d0f1b;
  }
  .main-nav__link {
    font-size: 16px;
  }
}
@media (min-width: 1360px) {
  .main-nav__toggle {
    top: 0;
    left: 0;
    width: 36px;
    height: 23px;
    background-size: 36px 23px;
  }
  .main-nav__wrapper {
    position: absolute;
    top: 0px;
    left: 55px;
    width: 280px;
    padding: 0;
    z-index: 100000;
    background-color: #1d0f1b;
  }
  .main-nav__list {
    border-left: none;
    padding: 30px;
  }
  .main-nav__link {
    display: block;
    position: relative;
    text-decoration: none;
    width: 100%;
    color: #fefeff;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    transition: text-decoration 0.5s;
  }
  .main-nav__link:hover,
  .main-nav__link:focus {
    color: #fefeff;
  }
  .main-nav__link::after {
    position: absolute;
    left: 0;
    bottom: -2px;
    content: "";
    width: 0;
    height: 2px;
    opacity: 0;
    background-color: #e94646;
    transition: width 0.5s, opacity 1s;
    transition-delay: 0.1s;
  }
  .main-nav__link:hover.main-nav__link::after,
  .main-nav__link:focus.main-nav__link::after {
    width: 100%;
    opacity: 1;
  }
}
