.wrapper {
  max-width: 1480px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 20px;
}
.slide {
  outline: none;
  margin: 0 10px;
  position: relative;
}
.content {
  height: 400px;
  max-width: 1010px;
  width: 100%;
  margin: 0 auto;
  transition: all 0.8s;
  border-radius: 4px;
}
.slide__video {
  display: none;
  pointer-events: none;
}
.slide__video-active {
  display: block;
}
.slide__video-active:hover {
  border: 10px solid red;
}
.slide__video-active:hover .btn-pause {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.slide__img {
  overflow: hidden;
  position: relative;
}
.slide__img::before {
  z-index: 10;
  display: block;
  position: absolute;
  content: '';
  width: 100%;
  bottom: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6) 0%, rgba(0, 0, 0, 0) 100%);
  height: 179px;
}
.slide__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
}
.slide__img-none {
  display: none;
}
.slick-active .content,
.slick-current .content {
  height: 530px;
}
.slick-slider .slick-track {
  display: flex;
  align-items: center;
}
.btn-play,
.btn-pause {
  display: none;
  z-index: 20;
  border: none;
  outline: none;
  height: 100px;
  width: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: all 0.3s;
  color: #fff;
  justify-content: center;
  align-items: center;
}
.btn-play:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.btn-pause:hover {
  display: flex;
}
.slick-current .btn-play {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.btn-play img,
.btn-pause img {
  width: 33px;
  height: 39px;
  margin-left: 6px;
  cursor: pointer;
}
.btn-pause img {
  margin: 0 auto;
}
.btn-play img.pause {
  margin-left: 0;
}
.video-slider .slick-list.draggable {
  height: 320px;
}
